import { Component, OnInit } from '@angular/core';
import * as Fingerprint2 from 'fingerprintjs2';
import { LoginService } from '../login.service';
import { DialogueService } from 'src/app/shared/commmon/dialogue/dialogue.service';
import { MatDialog } from '@angular/material';
import { CommonService } from 'src/app/shared/services/common.service';

@Component({
  selector: 'app-signup',
  templateUrl: './signup.component.html',
  styleUrls: ['./signup.component.scss']
})
export class SignupComponent implements OnInit {
  FullName = '';
  Email = '';
  PhoneNumber = '';
  PassWord = '';
  RetypePassWord = '';
  BrowserFingerPrint = '';
  Response = '';
  DisableButton = false;


  constructor(private service: LoginService, private loader: DialogueService,
    public dialog: MatDialog, private common: CommonService) {

  }
  // get browser fingerprint
  GetBrowserFingerPrint() {
    Fingerprint2.get((components) => {
      const values = components.map(function (component) { return component.value; });
      this.BrowserFingerPrint = Fingerprint2.x64hash128(values.join(''), 31);
    });
  }
  // check username availability

  // register a new Customer
  RegisterCustomer() {
    if (this.FullName === '' || this.FullName === undefined || this.FullName == null) {
      this.Response = 'Name Required';
      return;
    } else {
      this.Response = '';
    }
    if (this.Email === '' || this.Email === undefined || this.Email == null) {
      this.Response = 'Email Required';
      return;
    } else {
      this.Response = '';
    }
    if (!this.ValidateEmail(this.Email)) {
      this.Response = 'Enter Correct Email Address';
      return;
    } else {
      this.Response = '';
    }
    if (this.PhoneNumber === '' || this.PhoneNumber === undefined || this.PhoneNumber == null) {
      this.Response = 'Phone Required';
      return;
    } else {
      this.Response = '';
    }
    if (this.PassWord === '' || this.PassWord === undefined || this.PassWord == null) {
      this.Response = 'Password Required';
      return;
    } else {
      this.Response = '';
    }
    if (!this.ValidatePassWord(this.PassWord)) {
      return;
    }

    const body = {
    Username: this.Email,
    PassWord: this.PassWord,
    RoleID: 4,
    Email: this.Email,
    BrowserFingerPrint: this.BrowserFingerPrint,
    MobileNo: this.PhoneNumber,
    IsLocked: 0
  };
  this.service.CustomerSignUp(body).subscribe(Response => {
      if (Response.Status === true) {
        this.loader.SetData({Status: '', Route: '/login', Message: 'Registration Successful'});
      }
  });
}

  ngOnInit() {
  }

  ValidateEmail(email) {
    // tslint:disable-next-line:max-line-length
    const EmailExpression =  new RegExp(/^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/);
    return EmailExpression.test(String(email).toLowerCase());
}
  ValidatePassWord(PassWord) {
    if (PassWord.length < 6) {
      this.Response = 'Password should have at least 6 elements';
      return false;
    }
    const PasswordExpression = new RegExp('^(?=.*[A-Za-z])(?=.*\\d)[A-Za-z\\d]{6,}$');
    if (!PasswordExpression.test(PassWord)) {
      this.Response = 'Password should have at least one element and one number';
      return false;
    }
    return true;
  }
  // check username availability
  CheckUserNameAvailability() {
    this.common.CheckUserNameAvailability(this.Email).subscribe(Response => {
      if (Response.Status === true) {
        this.DisableButton = false;
        this.Response = '';
      } else {
        this.DisableButton = true;
        this.Response = 'An account is already registered with this e-mail/Username';
      }
    });
  }

}




